<template>
  <div>
    <v-btn :value="showDialog" :style="buttonStyles" fab @click="showDialog = !showDialog">
      <img :src="require('../../assets/label_override.svg')" :style="iconStyles" />
    </v-btn>
    <v-dialog v-model="showDialog" max-width="500px" dark>
      <v-card :color="colors.primaryColour">
        <v-card-title class="headline dialogHeader" :style="headerStyles">
          <v-row>
            <v-col><span class="headline">Player Labels</span></v-col>
            <v-col class="text-right">
              <v-btn @click="flipLabels" class="mr-2">Flip labels</v-btn>
              <v-btn @click="resetPlayersMap">Reset</v-btn>
            </v-col>
          </v-row>
        </v-card-title>
        <v-card-text style="padding-top: 15px;">
          <v-container>
            <v-form v-if="playModel && playModel.playData.players.length" v-model="isValid" autocomplete="off">
              <v-row v-for="(player, i) in playModel.playData.players" :key="i" style="align-items: baseline;">
                <v-col cols="1" style="text-align: left;">
                  <span class="player-position">{{ player.position }}</span>
                </v-col>
                <v-col cols="1" style="text-align: center;">
                  <span>=</span>
                </v-col>
                <v-col cols="7" class="pr-4">
                  <v-select
                    v-if="teamPlayers.length"
                    :items="teamPlayers"
                    item-text="User_Name"
                    item-value="id"
                    hide-details
                    :value="teamPlayerOnPosition(player.position)"
                    :clearable="true"
                    @change="changePlayer($event, player)"
                  ></v-select>
                  <v-text-field
                    v-else
                    style="margin: 0; padding: 0;"
                    :rules="[
                      value => {
                        return !value || value.length <= 3 || 'Max 3 characters';
                      },
                    ]"
                    :value="playerLabelOnPosition(player.position)"
                    @change="changePlayerLabel($event, player)"
                  ></v-text-field>
                </v-col>
                <v-col cols="3">
                  <v-select
                    v-if="playerHats.length"
                    :items="playerHats"
                    item-text="key"
                    item-value="key"
                    hide-details
                    :value="playerHatOnPosition(player.position)"
                    :clearable="true"
                    class="player-hat-select"
                    @change="changePlayerHat($event, player)"
                  >
                    <template v-slot:item="{ item }">
                      <img :src="item.image" style="max-width: 40px; max-height: 40px;" />
                    </template>
                    <template v-slot:selection="{ item }">
                      <img :src="item.image" style="max-width: 40px; max-height: 20px;" />
                    </template>
                  </v-select>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <div class="flex-grow-1"></div>
          <v-btn color="dialogBut1" text @click="closeDialog">Cancel</v-btn>
          <v-btn :color="colors.tertiaryColour" :disabled="!isValid" @click.prevent="handleSubmit">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import PlayerOverridesMixin from '../../mixins/PlayerOverridesMixin';
export default {
  name: 'LabelsOverrideField',
  mixins: [PlayerOverridesMixin],
  props: ['data'],
  data: () => ({
    buttonSize: 40,
    showDialog: false,
  }),
  computed: {
    ...mapState({
      playModel: state => state.playModel,
    }),
    colors() {
      return this.options.TeamColours;
    },
    flipPlayerLabelsOption() {
      return this.options.FlipPlayerLabels;
    },
    headerStyles() {
      return {
        backgroundColor: `${this.colors.tertiaryColour} !important`,
      };
    },
    buttonStyles() {
      return {
        width: `${this.buttonSize}px`,
        height: `${this.buttonSize}px`,
        background: this.showDialog ? this.colors.tertiaryColour : this.colors.secondaryColour,
        color: '#fff',
        'font-weight': 600,
      };
    },
    iconStyles() {
      return {
        width: '30px',
        height: '30px',
      };
    },
  },
  methods: {
    handleSubmit() {
      this.$emit('changed', this.playersMap);

      this.closeDialog();
    },
    flipLabels() {
      this.$eventHub.$emit('onFieldValueChange', {
        FlipPlayerLabels: !this.flipPlayerLabelsOption,
      });
    },
    closeDialog() {
      this.showDialog = false;
    },
  },
};
</script>

<style scoped>
.col {
  padding: 0;
}
.player-position {
  background-color: #fff;
  color: #000;
  padding: 10px 14px;
  border-radius: 50%;
}
</style>
<style>
.player-hat-select .v-select__selections {
  align-items: baseline !important;
}
</style>
