<template>
  <v-btn :value="fieldValue" :style="buttonStyles" fab @click="fieldValue = !fieldValue">1x1</v-btn>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'ShowFirstFrameField',
  props: ['data'],
  data: () => ({
    buttonSize: 40,
  }),
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    colors() {
      return this.options.TeamColours;
    },
    fieldValue: {
      get: function () {
        return this.data.value;
      },
      set: function (newVal) {
        this.$emit('changed', newVal);
      },
    },
    buttonStyles() {
      return {
        width: `${this.buttonSize}px`,
        height: `${this.buttonSize}px`,
        background: this.fieldValue ? this.colors.tertiaryColour : this.colors.secondaryColour,
        color: '#fff',
        'text-transform': 'lowercase',
        'font-weight': 600,
      };
    },
  },
};
</script>

<style scoped>
.activeClass {
  background-color: #ff8000;
  caret-color: #ff8000;
  color: #ff8000;
  opacity: 1;
}
.activeClass:before {
  opacity: 0 !important;
}
</style>
