<template>
  <v-slider
    v-model="fieldValue"
    hide-details
    min="0"
    :max="maxLineAnimation"
    :color="colors.tertiaryColour"
    @change="onChange"
    @start="onStart"
    @end="onEnd"
  >
    <slot name="prepend">
      <span>X</span>
    </slot>
  </v-slider>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import _ from 'lodash';
export default {
  name: 'SliderField',
  props: ['data'],
  data: () => ({
    errorMsg: '',
    sendEvents: false,
  }),
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    ...mapGetters({
      linesOnCourt: 'getPlayDataLines',
    }),
    colors() {
      return this.options.TeamColours;
    },
    fieldValue: {
      get() {
        return this.data.value * 100;
      },
      set(newVal) {
        if (this.sendEvents) {
          this.$emit('changed', newVal / 100);
        }
      },
    },
    maxLineAnimation() {
      return (
        this.linesOnCourt.reduce((result, line) => {
          line.animations.forEach(animation => {
            const animationKeyTimeMax = _.max(animation.keyTimes);
            if (animationKeyTimeMax && result < animationKeyTimeMax) {
              result = animationKeyTimeMax;
            }
          });
          return result;
        }, 0) * 100
      );
    },
  },
  created() {
    this.$eventHub.$on('optionsChanged', () => {
      this.appendSliderHandle();
    });
  },
  mounted() {
    this.appendSliderHandle();
  },
  methods: {
    onStart() {
      this.sendEvents = true;
    },
    onEnd() {
      this.sendEvents = false;
    },
    onChange(newVal) {
      this.$emit('changed', newVal / 100);
    },
    appendSliderHandle() {
      const svgNS = 'http://www.w3.org/2000/svg';
      const svg = document.createElementNS(svgNS, 'svg');
      svg.setAttribute('width', 72);
      svg.setAttribute('height', 72);

      const circle = document.createElementNS(svgNS, 'circle');
      circle.setAttribute('cx', 36);
      circle.setAttribute('cy', 36);
      circle.setAttribute('r', 18);
      circle.setAttribute('fill', this.colors.tertiaryColour);
      svg.appendChild(circle);

      const el = document.querySelector('.v-application .v-slider__thumb-container');
      el.innerHTML = '';
      svg.classList.add('v-slider__thumb', 'primary');
      el.append(svg);
    },
  },
};
</script>

<style>
.v-application .v-slider__thumb.primary {
  width: 72px;
  height: 72px;
  left: -36px;
  background: transparent !important;
}
.v-application .v-slider__thumb.primary:before {
  display: none;
}
.v-application .v-slider__thumb-container--active .v-slider__thumb:before {
  transform: scale(1) !important;
}
</style>
