<template>
  <v-chip-group v-model="fieldValue" active-class="activeClass" mandatory>
    <v-chip v-for="(item, idx) in items" :key="item.value" :value="item.value" :style="activeStyle(item.value, idx)">
      <img :src="item.src" class="icon" />
    </v-chip>
  </v-chip-group>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'MirrorModeField',
  props: ['data'],
  data: () => {
    return {
      items: [
        {
          src: require('../../assets/mirror_left.png'),
          value: false,
        },
        {
          src: require('../../assets/mirror_right.png'),
          value: true,
        },
      ],
    };
  },
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    colors() {
      return this.options.TeamColours;
    },
    fieldValue: {
      get: function () {
        return this.data.value;
      },
      set: function (newVal) {
        this.$emit('changed', newVal);
      },
    },
  },
  methods: {
    activeStyle(value, idx) {
      return {
        'background-color': value === this.fieldValue ? this.colors.tertiaryColour : this.colors.secondaryColour,
        'caret-color': value === this.fieldValue ? this.colors.tertiaryColour : this.colors.secondaryColour,
        color: value === this.fieldValue ? this.colors.tertiaryColour : this.colors.secondaryColour,
        margin: 0,
        'margin-left': `${idx === 0 ? 0 : 8}px`,
      };
    },
  },
};
</script>

<style scoped>
.activeClass {
  opacity: 1;
}
.activeClass:before {
  opacity: 0 !important;
}
.icon {
  width: 30px;
  height: 30px;
}
@media only screen and (max-width: 600px) {
  .icon {
    width: 23px;
    height: 23px;
  }
}
</style>
