import { Animation } from '@luceosports/play-rendering';

const state = {
  animationModel: null,
  isRunning: false,
  isVisible: true,
};

const getters = {
  hasProgress(state) {
    return state.animationModel && !!state.animationModel.globalProgress;
  },
  isRunning(state) {
    return state.animationModel && state.isRunning;
  },
};

const mutations = {
  setAnimationModel(state, payload) {
    state.animationModel = payload;
  },
  startRunning(state) {
    state.isRunning = true;
  },
  stopRunning(state) {
    state.isRunning = false;
  },
  setVisible(state) {
    state.isVisible = true;
  },
  setHidden(state) {
    state.isVisible = false;
  },
};

const actions = {
  initAnimationModel({ commit, state, rootState }, canvasCtx) {
    if (!state.animationModel) {
      commit('setAnimationModel', new Animation(canvasCtx, rootState.playModel));
    }
  },
  destroyAnimationModel({ commit }) {
    commit('setAnimationModel', null);
  },
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
