<template>
  <div v-show="displayButton" class="play-btn-container" :style="containerStyles">
    <v-btn
      v-show="!running"
      class="mx-0"
      fab
      dark
      :color="colors.tertiaryColour"
      :style="buttonStyles"
      @click="animationStart"
    >
      <v-icon :size="iconSize" dark>mdi-play</v-icon>
    </v-btn>
    <v-btn
      v-show="running"
      class="mx-0"
      fab
      dark
      :color="colors.tertiaryColour"
      :style="buttonStyles"
      @click="animationPause"
    >
      <v-icon :size="iconSize" dark>mdi-pause</v-icon>
    </v-btn>
  </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'PlayButton',
  props: ['containerWidth'],
  data: () => {
    return {
      displayButton: false,
    };
  },
  computed: {
    ...mapState('animation', {
      running: state => state.isRunning,
    }),
    ...mapState({
      options: state => state.options,
    }),
    colors() {
      return this.options.TeamColours;
    },
    buttonSize() {
      return this.$vuetify.breakpoint.smAndUp ? 80 : 40;
    },
    iconSize() {
      return this.buttonSize * 0.7;
    },
    containerStyles() {
      return {
        position: 'absolute',
        bottom: `-3%`,
        left: `${this.containerWidth / 2 - this.buttonSize / 2}px`,
      };
    },
    buttonStyles() {
      return {
        width: `${this.buttonSize}px`,
        height: `${this.buttonSize}px`,
      };
    },
  },
  created() {
    this.$eventHub.$on('drawPreviewFramesCompleted', () => {
      this.displayButton = true;
    });
  },
  methods: {
    animationStart() {
      this.$eventHub.$emit('animationPlay');
    },
    animationPause() {
      this.$eventHub.$emit('animationPause');
    },
  },
};
</script>
