import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';
import store from './store';

Vue.config.productionTip = false;

const eventHub = new Vue();
Vue.prototype.$eventHub = eventHub;

new Vue({
  vuetify,
  store,
  created: function () {
    if (window.teamPlayers) store.commit('setTeamPlayers', window.teamPlayers);
    if (window.play) store.commit('setPlayData', window.play);
    if (window.options) store.commit('setOptions', window.options);
  },
  render: h => h(App),
}).$mount('#app');
