<template>
  <div>
    <v-btn :style="buttonStyles" fab @click="fieldValue = oppositeValue.value"
      ><v-icon v-if="oppositeValue.icon" large>{{ oppositeValue.icon }}</v-icon></v-btn
    >
  </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'ToggleIconField',
  props: ['data'],
  data: () => ({
    buttonSize: 40,
  }),
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    colors() {
      return this.options.TeamColours;
    },
    oppositeValue() {
      return this.data.items.find(item => item.value !== this.fieldValue);
    },
    fieldValue: {
      get: function () {
        return this.data.value;
      },
      set: function (newVal) {
        this.$emit('changed', newVal);
      },
    },
    buttonStyles() {
      const selectedItem = this.data.items.find(item => item.value === this.fieldValue);
      return {
        color: '#fff',
        width: `${this.buttonSize}px`,
        height: `${this.buttonSize}px`,
        'background-color': selectedItem.highlight ? this.colors.tertiaryColour : this.colors.secondaryColour,
        'background-image': selectedItem.src ? `url('${selectedItem.src}')` : 'none',
        'background-size': `${this.buttonSize - 10}px`,
        'background-position': 'center',
      };
    },
  },
};
</script>

<style scoped>
.v-icon.v-icon.v-icon--link:after {
  background: transparent !important;
}
.v-btn:before {
  opacity: 0 !important;
}
</style>
