<template>
  <div>
    <div :style="buttonStyles" @click="fieldValue = oppositeValue.value"></div>
  </div>
</template>

<script>
export default {
  name: 'CourtBackgroundField',
  props: ['data'],
  data: () => {
    return {
      items: [
        {
          src: require('../../assets/full-court-hardwood.png'),
          value: 'Hardwood',
        },
        {
          src: require('../../assets/full-court-white.png'),
          value: 'White court',
        },
      ],
      buttonSize: {
        width: 40,
        height: 36,
      },
    };
  },
  computed: {
    oppositeValue() {
      return this.items.find(item => item.value !== this.fieldValue);
    },
    fieldValue: {
      get: function () {
        return this.data.value;
      },
      set: function (newVal) {
        this.$emit('changed', newVal);
      },
    },
    buttonStyles() {
      return {
        width: `${this.buttonSize.width}px`,
        height: `${this.buttonSize.height}px`,
        'background-image': `url('${this.oppositeValue.src}')`,
        'background-size': `${this.buttonSize.width}px ${this.buttonSize.height * 2}px`,
      };
    },
  },
};
</script>

<style scoped></style>
