import _ from 'lodash';
import { mapState } from 'vuex';
export default {
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    fields() {
      return this.data.fieldData.map(field => {
        const fieldTypeAttrs = {
          value: _.get(this.options, field.key),
        };
        if (['ToggleButtonField', 'SelectField'].includes(field.type)) {
          fieldTypeAttrs.items = this.selectFieldOptions(field.items);
        }
        return { ...field, ...fieldTypeAttrs };
      });
    },
  },
  methods: {
    selectFieldOptions(items) {
      return _.map(_.sortBy(items, ['priority']), item => {
        return {
          value: typeof item.value !== 'undefined' ? item.value : item.name,
          text: _.startCase(item.name),
        };
      });
    },
    fieldValueChanged(value, { key }) {
      const field = this.data.fieldData.find(item => item.key === key);
      if (field.onChangeCallback) field.onChangeCallback(value);
      if (!field.disableEmitting) {
        this.$eventHub.$emit('onFieldValueChange', { [key]: value });
      }
    },
  },
};
