<template>
  <v-chip-group v-model="fieldValue" active-class="activeClass" mandatory>
    <v-chip v-for="item in items" :key="item.value" :value="item.value" :style="activeStyle(item.value)">
      <img :src="item.src" class="icon" />
    </v-chip>
  </v-chip-group>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'PlaybackSpeedField',
  props: ['data'],
  data: () => {
    return {
      items: [
        {
          text: '0.5x',
          src: require('../../assets/speed_0.5x.svg'),
          value: 0.5,
        },
        {
          text: '1x',
          src: require('../../assets/speed_2x.svg'),
          value: 1,
        },
        {
          text: '2x',
          src: require('../../assets/speed_3x.svg'),
          value: 2,
        },
      ],
    };
  },
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    colors() {
      return this.options.TeamColours;
    },
    fieldValue: {
      get: function () {
        return this.data.value;
      },
      set: function (newVal) {
        this.$emit('changed', newVal);
      },
    },
  },
  methods: {
    activeStyle(value) {
      return {
        'background-color': value === this.fieldValue ? this.colors.tertiaryColour : this.colors.secondaryColour,
        'caret-color': value === this.fieldValue ? this.colors.tertiaryColour : this.colors.secondaryColour,
        color: value === this.fieldValue ? this.colors.tertiaryColour : this.colors.secondaryColour,
      };
    },
  },
};
</script>

<style scoped>
.activeClass {
  background-color: #ff8000;
  caret-color: #ff8000;
  color: #ff8000;
  opacity: 1;
}
.activeClass:before {
  opacity: 0 !important;
}
.icon {
  width: 30px;
  height: 30px;
}
@media only screen and (max-width: 600px) {
  .icon {
    width: 20px;
    height: 20px;
  }
}
</style>
