import Vue from 'vue';
import Vuex from 'vuex';
import animation from './modules/animation';
import { Play } from '@luceosports/play-rendering';

Vue.use(Vuex);

export default new Vuex.Store({
  modules: {
    animation,
  },

  state: {
    playData: null,
    playModel: null,
    teamPlayers: [],
    options: {
      TopControlsDisplay: false,
      CourtBackground: 'Hardwood',
      MirrorMode: false,
      FlipPlayerLabels: false,
      PlaybackSpeed: 1,
      AnimationProgress: 0,
      ShowFirstFrame: true,
      LabelsOverrideMap: [],
      LabelsOverrideType: 'Initials',
      LinesDisplay: true,
      LinesDisplayOnMoveOnly: true,
      LinesHiddenIds: [],
      PlayersHiddenPositions: [],
      Position: null,
      TeamColours: {
        tertiaryColour: '#f36f21',
        secondaryColour: '#424242',
        primaryColour: '#303030',
      },
    },
  },
  mutations: {
    setTeamPlayers(state, payload) {
      state.teamPlayers = payload;
    },
    setPlayData(state, payload) {
      state.playData = payload;
    },
    setPlayModel(state, width) {
      if (state.playData) {
        state.playModel = new Play(state.playData, {
          width,
          lineColor: '#fff',
          background: 'Hardwood',
        });
      }
    },
    setPlayModelOptions(state, options) {
      if (state.playModel) state.playModel.setOptions(options);
    },
    setOptions(state, payload) {
      Object.assign(state.options, payload);
    },
    setPlayDataPlayerLabels(state, playersMap) {
      state.playModel.playData.players = state.playModel.playData.players.map(player => {
        if (typeof player.textOverrideDefault === 'undefined') player.textOverrideDefault = player.textOverride || '';
        player.textOverride = player.textOverrideDefault;
        player.playerHatKey = '';
        const playerMapItem = playersMap.find(p => p.position === player.position);
        if (playerMapItem) {
          if (playerMapItem.playerLabel) {
            player.textOverride = playerMapItem.playerLabel;
          }
          if (playerMapItem.playerId) {
            const teamPlayer = state.teamPlayers.find(p => p.id === playerMapItem.playerId);
            if (teamPlayer) {
              player.textOverride = teamPlayer.User_Name.split(' ')
                .map(n => n[0])
                .join('')
                .slice(0, 2);

              if (state.options.LabelsOverrideType === 'Jersey number') {
                player.textOverride = teamPlayer.jersey_number || ' ';
              }
            }
          }
          if (playerMapItem.playerHatKey) {
            player.playerHatKey = playerMapItem.playerHatKey;
          }
        }
        return player;
      });
    },
  },
  getters: {
    getTeamPlayers(state) {
      return state.teamPlayers.sort((a, b) => {
        if (a.User_Name < b.User_Name) return -1;
        if (a.User_Name > b.User_Name) return 1;
        return 0;
      });
    },
    getPlayDataLines(state) {
      if (!state.playData) return [];
      return state.playData.playData.lines;
    },
  },
});
