            this.$eventHub.$emit('animationProgressChange', value);
          },
          disableEmitting: true,
        },
        {
          key: 'PlaybackSpeed',
          type: 'PlaybackSpeedField',
          label: 'Playback Speed',
          style: { 'justify-content': 'flex-end' },
          size: { sm: 4, md: 3 },
        },
      ],
    };
  },
