import { mapGetters, mapMutations, mapState } from 'vuex';

export default {
  data: () => ({
    playersMap: [],
    isValid: true,
    playerHats: [
      {
        key: 'santa',
        image: require('../assets/hats/santa.svg'),
      },
      {
        key: 'santa_reverse',
        image: require('../assets/hats/santa_reverse.svg'),
      },
      {
        key: 'elf',
        image: require('../assets/hats/elf.svg'),
      },
      {
        key: 'elf_reverse',
        image: require('../assets/hats/elf_reverse.svg'),
      },
      {
        key: 'leprechaun',
        image: require('../assets/hats/leprechaun.svg'),
      },
      {
        key: 'leprechaun_reverse',
        image: require('../assets/hats/leprechaun_reverse.svg'),
      },
      {
        key: 'winter',
        image: require('../assets/hats/winter.svg'),
      },
      {
        key: 'winter_reverse',
        image: require('../assets/hats/winter_reverse.svg'),
      },
      {
        key: 'witch',
        image: require('../assets/hats/witch.svg'),
      },
      {
        key: 'witch_reverse',
        image: require('../assets/hats/witch_reverse.svg'),
      },
      {
        key: 'cowboy',
        image: require('../assets/hats/cowboy.svg'),
      },
      {
        key: 'viking',
        image: require('../assets/hats/viking.svg'),
      },
      {
        key: 'hair',
        image: require('../assets/hats/hair.svg'),
      },
      {
        key: 'beard',
        image: require('../assets/hats/beard.svg'),
      },
    ],
  }),
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    ...mapGetters({
      teamPlayers: 'getTeamPlayers',
    }),
  },
  watch: {
    'options.LabelsOverrideMap': function (val) {
      this.playersMap = val;
    },
  },
  methods: {
    ...mapMutations({
      vuexSetOptions: 'setOptions',
    }),
    teamPlayerOnPosition(position) {
      const playerMapItem = this.playersMap.find(item => item.position === position);
      return playerMapItem ? this.teamPlayers.find(p => p.id === playerMapItem.playerId) : null;
    },
    playerHatOnPosition(position) {
      const playerMapItem = this.playersMap.find(item => item.position === position);
      return playerMapItem ? this.playerHats.find(h => h.key === playerMapItem.playerHatKey) : null;
    },
    playerLabelOnPosition(position) {
      const playerMapItem = this.playersMap.find(item => item.position === position);
      return playerMapItem ? playerMapItem.playerLabel : null;
    },
    changePlayer(teamPlayerId, playDataPlayer) {
      let playerMapItem = {
        position: playDataPlayer.position,
        playerLabel: null,
        playerHatKey: null,
      };
      this.playersMap = this.playersMap.filter(item => {
        if (item.position === playDataPlayer.position) {
          playerMapItem = item;
          return false;
        }
        return true;
      });
      playerMapItem.playerId = teamPlayerId || null;
      this.addPlayerMapItem(playerMapItem);
    },
    changePlayerLabel(playerLabel, playDataPlayer) {
      let playerMapItem = {
        position: playDataPlayer.position,
        playerHatKey: null,
        playerId: null,
      };
      this.playersMap = this.playersMap.filter(item => {
        if (item.position === playDataPlayer.position) {
          playerMapItem = item;
          return false;
        }
        return true;
      });
      playerMapItem.playerLabel = playerLabel || null;
      this.addPlayerMapItem(playerMapItem);
    },
    changePlayerHat(playerHatKey, playDataPlayer) {
      let playerMapItem = {
        position: playDataPlayer.position,
        playerLabel: null,
        playerId: null,
      };
      this.playersMap = this.playersMap.filter(item => {
        if (item.position === playDataPlayer.position) {
          playerMapItem = item;
          return false;
        }
        return true;
      });
      playerMapItem.playerHatKey = playerHatKey || null;
      this.addPlayerMapItem(playerMapItem);
    },
    addPlayerMapItem(playerMapItem) {
      if (playerMapItem.playerHatKey || playerMapItem.playerLabel || playerMapItem.playerId) {
        this.playersMap.push(playerMapItem);
      }
    },
    resetPlayersMap() {
      this.playersMap = [];
    },
  },
};
