<template>
  <v-row>
    <v-col
      v-for="item in fields"
      v-show="showInToolbar(item)"
      :key="item.key"
      :cols="item.size.xs || 6"
      :sm="item.size.sm || 12"
      :md="item.size.md || item.size.sm || 12"
      class="py-0 px-1 component-wrap"
      :style="item.style || {}"
      :class="item.class || ''"
    >
      <component
        :is="item.type"
        v-if="item.key"
        :data="item"
        :toolbar-component="toolbarComponent"
        @changed="fieldValueChanged($event, item)"
      ></component>
    </v-col>
  </v-row>
</template>

<script>
import OptionsMixin from '../mixins/OptionsMixin';
import MirrorModeField from './fields/MirrorModeField';
import CourtBackgroundField from './fields/CourtBackgroundField';
import ShowFirstFrameField from './fields/ShowFirstFrameField';
import LabelsOverrideField from './fields/LabelsOverrideField';
import SelectField from './fields/SelectField';
import ToggleIconField from './fields/ToggleIconField';
import SliderField from './fields/SliderField';
import PlaybackSpeedField from './fields/PlaybackSpeedField';
import ToolbarFieldsRow from './ToolbarFieldsRow';
import { mapState } from 'vuex';
export default {
  name: 'ToolbarFieldsRow',
  components: {
    MirrorModeField,
    CourtBackgroundField,
    ShowFirstFrameField,
    LabelsOverrideField,
    SelectField,
    ToggleIconField,
    SliderField,
    PlaybackSpeedField,
    ToolbarFieldsRow,
  },
  mixins: [OptionsMixin],
  props: ['data', 'toolbarComponent'],
  computed: {
    ...mapState({
      options: state => state.options,
    }),
  },
  methods: {
    showInToolbar(field) {
      if (this.toolbarComponent !== 'HeaderToolbar' || field.key === 'TopControlsDisplay') return true;
      return this.options.TopControlsDisplay;
    },
  },
};
</script>

<style scoped>
.component-wrap {
  align-items: center;
  display: flex;
}
</style>
