<template>
  <div v-show="loading" class="custom-loader" :style="wrapStyles">
    <div style="display: flex; justify-content: center; flex-direction: column; align-items: center;">
      <v-progress-circular :size="80" :color="spinnerColor" indeterminate class="mt-5"></v-progress-circular>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: 'Preloader',
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    ...mapState({
      options: state => state.options,
    }),
    wrapStyles() {
      return {
        backgroundColor: `${this.options.TeamColours.primaryColour}`,
      };
    },
    spinnerColor() {
      return `${this.options.TeamColours.tertiaryColour}` || 'deep-orange lighten-2';
    },
  },
  created: function () {
    this.$eventHub.$on(['preloaderShow'], this.preloaderShow);
    this.$eventHub.$on(['preloaderHide'], this.preloaderHide);
  },
  beforeDestroy: function () {
    this.$eventHub.$off(['preloaderShow', 'preloaderHide']);
  },
  methods: {
    preloaderShow: function () {
      this.loading = true;
    },
    preloaderHide: function () {
      this.loading = false;
    },
  },
};
</script>

<style scoped>
.custom-loader {
  width: 100%;
  min-height: 100%;
  background-color: #303030;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 201;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
