<template>
  <div class="play-animation-box">
    <canvas ref="animation-canvas" @click="onCanvasClick"></canvas>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex';
export default {
  name: 'PlayAnimation',
  computed: {
    ...mapState({
      playModel: state => state.playModel,
      options: state => state.options,
    }),
    ...mapState('animation', {
      animationModel: state => state.animationModel,
    }),
    ...mapGetters('animation', ['isRunning']),
  },
  watch: {
    playModel: {
      handler() {
        this.showFirstFrame();
      },
    },
  },
  created() {
    this.$eventHub.$on('animationPlay', this.start);
    this.$eventHub.$on('animationPause', this.pause);
    this.$eventHub.$on('animationHide', () => {
      this.pause();
      this.vuexSetAnimationHidden();
    });

    this.$eventHub.$on('animationRefreshFrame', () => {
      if (!this.playModel) return;
      const progress = this.animationModel ? this.animationModel.globalProgress : 0;
      this.refreshAnimation();
      this.animationModel.setProgress(progress).drawPreviewFrame();
      this.options.ShowFirstFrame ? this.vuexSetAnimationVisible() : this.vuexSetAnimationHidden();
    });

    this.$eventHub.$on('animationProgressChange', progress => {
      this.initAnimation();
      this.vuexSetOptions({ AnimationProgress: progress, ShowFirstFrame: true });
      this.vuexSetAnimationVisible();
      this.animationModel.setProgress(progress).drawPreviewFrame();
      if (this.isRunning) {
        this.pause();
        setTimeout(() => {
          this.start();
        }, 500);
      }
    });
  },
  beforeDestroy() {
    this.endAnimation();
  },
  methods: {
    ...mapActions('animation', {
      vuexInitAnimationModel: 'initAnimationModel',
      vuexDestroyAnimationModel: 'destroyAnimationModel',
    }),
    ...mapMutations({
      vuexSetOptions: 'setOptions',
    }),
    ...mapMutations('animation', {
      vuexSetAnimationHidden: 'setHidden',
      vuexSetAnimationVisible: 'setVisible',
      vuexStartAnimationRunning: 'startRunning',
      vuexStopAnimationRunning: 'stopRunning',
    }),
    start() {
      this.vuexSetAnimationVisible();
      this.vuexStartAnimationRunning();
      this.vuexSetOptions({ ShowFirstFrame: true });

      this.initAnimation();

      this.animationModel.start(this.animationCompleted, this.onAnimationProgress);
    },
    pause() {
      this.vuexStopAnimationRunning();
      if (this.animationModel) this.animationModel.pause();
    },
    animationCompleted() {
      this.vuexStopAnimationRunning();
      this.vuexSetOptions({ AnimationProgress: 0 });
      this.showFirstFrame();
      this.$emit('completed');
    },
    onAnimationProgress(progress) {
      if (progress.toFixed(2) !== this.options.AnimationProgress.toFixed(2)) {
        this.vuexSetOptions({ AnimationProgress: progress });
      }
    },
    showFirstFrame() {
      this.initAnimation();
      this.animationModel.setProgress(0).drawPreviewFrame();
    },
    refreshAnimation() {
      this.endAnimation();
      this.initAnimation();
    },
    initAnimation() {
      this.vuexInitAnimationModel(this.$refs['animation-canvas'].getContext('2d'));
    },
    endAnimation() {
      this.pause();
      this.vuexDestroyAnimationModel();
    },
    onCanvasClick(e) {
      const { offsetX, offsetY } = e;
      const closestPlayer = this.animationModel.animationFrame.closestObjectToPoint(
        { x: offsetX, y: offsetY },
        'player'
      );
      if (closestPlayer && closestPlayer.distance < 2) {
        this.pause();
        this.$eventHub.$emit('playerOverrideDialog', { show: true, player: closestPlayer.object });
      }
    },
  },
};
</script>

<style scoped>
.play-animation-box {
  display: flex;
  position: relative;
}
@media only screen and (max-width: 600px) {
  .play-options-container {
    padding-top: 0px;
  }
}
</style>
