<template>
  <v-dialog :value="show" width="500" @input="changeDialogValue">
    <v-card :color="colors.primaryColour">
      <v-card-title class="headline dialogHeader" :style="headerStyles">Position {{ player.position }} </v-card-title>
      <v-card-text class="pt-2 pb-0">
        <v-form v-model="isValid" autocomplete="off">
          <v-row>
            <v-col cols="12">
              <v-select
                v-if="teamPlayers.length"
                :items="teamPlayers"
                item-text="User_Name"
                item-value="id"
                hide-details
                placeholder="Please select a player"
                :value="teamPlayerOnPosition(player.position)"
                :clearable="true"
                @change="changePlayer($event, player)"
              >
                <template v-slot:item="data">
                  <v-img :src="data.item.headshotUrl || defaultPlayerIcon" max-width="24" class="mr-2"></v-img>
                  {{ data.item.User_Name }} {{ data.item.jersey_number }}
                </template>
              </v-select>
              <v-text-field
                v-else
                style="margin: 0; padding: 0;"
                :rules="[
                  value => {
                    return !value || value.length <= 3 || 'Max 3 characters';
                  },
                ]"
                placeholder="Please override player label"
                :value="playerLabelOnPosition(player.position)"
                @change="changePlayerLabel($event, player)"
              ></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-switch v-model="inFocus" label="In focus"></v-switch>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <div class="flex-grow-1"></div>
        <v-btn color="dialogBut1" text @click="closeDialog">Cancel</v-btn>
        <v-btn :color="colors.tertiaryColour" :disabled="!isValid" @click.prevent="handleSubmit">Save</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import PlayerOverridesMixin from '../../mixins/PlayerOverridesMixin';

export default {
  name: 'PlayerOverrideDialog',
  mixins: [PlayerOverridesMixin],
  data: () => ({
    show: false,
    player: false,
    defaultPlayerIcon: require('../../assets/player-orange-ball.png'),
    inFocus: false,
  }),
  computed: {
    colors() {
      return this.options.TeamColours;
    },
    headerStyles() {
      return {
        backgroundColor: `${this.colors.tertiaryColour} !important`,
      };
    },
  },
  watch: {
    show(value) {
      if (value) {
        this.inFocus = this.options.Position === this.player.position;
      }
    },
  },
  created() {
    this.$eventHub.$on('playerOverrideDialog', ({ show, player }) => {
      this.show = show;
      this.player = player;
    });
  },
  methods: {
    changeDialogValue(value) {
      this.show = value;
    },
    closeDialog() {
      this.show = false;
    },
    handleSubmit() {
      this.$eventHub.$emit('onFieldValueChange', {
        LabelsOverrideMap: this.playersMap,
        Position: this.inFocus
          ? this.player.position
          : this.options.Position !== this.player.position
          ? this.options.Position
          : null,
      });

      this.closeDialog();
    },
  },
};
</script>

<style scoped></style>
