<template>
  <v-select
    v-model="fieldValue"
    :items="data.items"
    item-text="text"
    item-value="value"
    :rules="data.rules"
    :label="data.label"
    class="custom-select"
    :menu-props="{ nudgeBottom: 40 }"
    hide-details
    outlined
    dense
  >
  </v-select>
</template>

<script>
export default {
  name: 'SelectField',
  props: ['data'],
  computed: {
    fieldValue: {
      get: function () {
        return this.data.value;
      },
      set: function (newVal) {
        this.$emit('changed', newVal);
      },
    },
  },
};
</script>

<style>
@media only screen and (max-width: 600px) {
  .v-text-field.v-text-field--enclosed:not(.v-text-field--rounded) > .v-input__control > .v-input__slot {
    padding: 0 6px !important;
  }
  .px-0-sx {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
}
</style>
