<template>
  <v-app dark :class="{ fullScreen: !toolbarDisplay }" :style="themeColor">
    <Preloader></Preloader>
    <HeaderToolbar v-show="toolbarDisplay" class="py-0"></HeaderToolbar>
    <div ref="play-canvas-wrapper" :style="wrapperContainerStyle">
      <PlayAnimation v-show="showAnimation" @completed="animationCompleted"></PlayAnimation>
      <div v-show="!showAnimation" class="play-view-box">
        <div ref="play-canvas-container" class="play-canvas-container"></div>
      </div>
      <PlayButton v-show="toolbarDisplay" :container-width="containerWidth"></PlayButton>
    </div>
    <FooterToolbar v-show="toolbarDisplay"></FooterToolbar>
    <PlayerOverrideDialog></PlayerOverrideDialog>
  </v-app>
</template>

<script>
import { Play, Frame } from '@luceosports/play-rendering';
import Preloader from './components/Preloader';
import HeaderToolbar from './components/HeaderToolbar';
import PlayAnimation from './components/PlayAnimation';
import PlayButton from './components/PlayButton';
import FooterToolbar from './components/FooterToolbar';
import PlayerOverrideDialog from './components/dialogs/PlayerOverrideDialog';
import { mapState, mapGetters, mapMutations } from 'vuex';

const WINDOW_PADDING_OFFSET_X = 16;
const HEADER_TOOLBAR_HEIGHT = 64;
const FOOTER_TOOLBAR_HEIGHT = 72;

export default {
  name: 'App',
  components: { HeaderToolbar, PlayAnimation, PlayButton, FooterToolbar, Preloader, PlayerOverrideDialog },
  data: () => {
    return {
      toolbarDisplay: true,
      window: {
        width: 0,
        height: 0,
      },
    };
  },
  computed: {
    ...mapState({
      playData: state => state.playData,
      playModel: state => state.playModel,
      options: state => state.options,
    }),
    ...mapState('animation', {
      animation: state => state.animationModel,
      showAnimation: state => state.isVisible,
    }),
    ...mapGetters('animation', {
      animationHasProgress: 'hasProgress',
    }),
    courtSize() {
      if (this.playData) {
        return this.playData.playData.court.courtRect.size;
      }
      return { width: 0, height: 0 };
    },
    windowOffsetHeight() {
      if (this.toolbarDisplay) {
        return this.window.height - HEADER_TOOLBAR_HEIGHT - FOOTER_TOOLBAR_HEIGHT;
      }
      return this.window.height;
    },
    containerAnimationWidth() {
      return this.calculateContainerWidth();
    },
    containerPreviewWidth() {
      return this.calculateContainerWidth(true);
    },
    containerWidth() {
      return this.showAnimation ? this.containerAnimationWidth : this.containerPreviewWidth;
    },
    wrapperContainerStyle() {
      return {
        position: 'relative',
        width: `${this.containerWidth}px`,
      };
    },
    themeColor() {
      return {
        background: `${this.options.TeamColours.primaryColour} !important`,
      };
    },
  },
  created() {
    window.onresize = () => {
      this.$eventHub.$emit('preloaderShow');
      clearTimeout(window.resizedFinished);
      window.resizedFinished = setTimeout(() => {
        this.webViewRefreshFrames();
      }, 100);
    };

    this.registerParentFrameEventListeners();

    this.registerPlayRenderingEventListeners();

    this.registerLocalEventListeners();
  },
  async mounted() {
    this.windowRefreshSizeData();

    await Play.init();

    this.vuexSetPlayModel(this.containerWidth);

    this.optionsChanged(this.options, false);
  },
  methods: {
    ...mapMutations({
      vuexSetPlayData: 'setPlayData',
      vuexSetPlayModel: 'setPlayModel',
      vuexSetPlayModelOptions: 'setPlayModelOptions',
      vuexSetOptions: 'setOptions',
      vuexSetTeamPlayers: 'setTeamPlayers',
      vuexSetPlayDataPlayerLabels: 'setPlayDataPlayerLabels',
    }),
    ...mapMutations('animation', {
      vuexSetAnimationVisible: 'setVisible',
      vuexSetAnimationHidden: 'setHidden',
      vuexStopAnimationRunning: 'stopRunning',
    }),
    registerParentFrameEventListeners() {
      // Parent frame
      window.addEventListener('message', event => {
        window.dispatchEvent(new CustomEvent(event.data.name, { detail: event.data.detail }));
      });
      // WebView
      window.addEventListener('webViewPreloaderShow', () => {
        this.$eventHub.$emit('preloaderShow');
      });
      window.addEventListener('webViewSetPlayData', this.setPlayData);
      window.addEventListener('webViewRefreshFrames', this.webViewRefreshFrames);
      window.addEventListener('webViewSetOptions', this.webViewSetOptions);
      window.addEventListener('webViewSetTeamPlayers', this.webViewSetTeamPlayers);
      window.addEventListener('webViewAnimationStart', () => {
        this.$eventHub.$emit('animationPlay');
      });
      window.addEventListener('webViewAnimationStartIfStarted', () => {
        if (this.animationHasProgress) this.$eventHub.$emit('animationPlay');
      });
      window.addEventListener('webViewAnimationPause', () => {
        this.$eventHub.$emit('animationPause');
      });
      window.addEventListener('webViewHideToolbar', () => {
        this.toolbarDisplay = false;
        this.webViewRefreshFrames();
      });
      window.addEventListener('webViewShowToolbar', () => {
        this.toolbarDisplay = true;
        this.webViewRefreshFrames();
      });
    },
    registerPlayRenderingEventListeners() {
      window.addEventListener('@luceosports/play-rendering:lineAnimationFinished', e => {
        this.postMessageToParentFrame({
          type: 'lineAnimationFinished',
          payload: {
            line: { id: e.detail.id },
          },
        });
      });
    },
    registerLocalEventListeners() {
      this.$eventHub.$on('onFieldValueChange', this.optionsChanged);
    },
    calculateContainerWidth(isPreviewMode = false) {
      if (!this.playModel) return this.window.width;

      const isViewVertical = this.window.width < this.windowOffsetHeight;
      const isPlayFullCourt = this.courtSize.width / this.courtSize.height < 0.6;

      const maxLimit = isPlayFullCourt || !isViewVertical ? this.windowOffsetHeight : this.window.width;

      let playModelWidth = this.courtSize.width;
      if (isPreviewMode && this.playModel.totalPhasesCount === 2) {
        playModelWidth *= 2;
      }

      const courtRatio = playModelWidth / this.courtSize.height;

      const resultWidth = Math.floor(maxLimit * courtRatio);

      // console.log({ resultWidth, isPlayFullCourt, maxLimit, courtRatio, isPlayVertical, isViewVertical });

      return resultWidth >= this.window.width - WINDOW_PADDING_OFFSET_X * 2
        ? this.window.width - WINDOW_PADDING_OFFSET_X * 2
        : resultWidth;
    },
    drawPreviewFrames() {
      if (!this.playModel) return;
      this.vuexSetPlayModelOptions({ width: this.containerPreviewWidth });

      const containerEl = this.$refs['play-canvas-container'];
      containerEl.innerHTML = '';

      for (let phase = 1; phase <= this.playModel.totalPhasesCount; phase++) {
        const canvas = document.createElement('canvas');
        canvas.frame = new Frame(this.playModel, phase).setContext(canvas.getContext('2d'));
        canvas.addEventListener('click', e => {
          const { offsetX, offsetY } = e;

          const canvasScale = canvas.offsetWidth / this.playData.playData.court.courtRect.size.width;

          // Calculate base canvas scale
          const baseCourtOffsetX = offsetX / canvasScale;
          const baseCourtOffsetY = offsetY / canvasScale;

          // Convert ox and oy based on current playModelScale (NOTE: need refactor = move width and scale from Play model to Frame model)
          const ox = baseCourtOffsetX * this.playModel.scale;
          const oy = baseCourtOffsetY * this.playModel.scale;

          const closestPlayer = canvas.frame.closestObjectToPoint({ x: ox, y: oy }, 'player');
          if (closestPlayer && closestPlayer.distance < 2) {
            this.$eventHub.$emit('playerOverrideDialog', { show: true, player: closestPlayer.object });
          }
        });

        canvas.frame.draw();

        if (this.playModel.totalPhasesCount > 1) {
          const multiFrameWrapper = document.createElement('div');
          multiFrameWrapper.style.width = `${Math.floor(this.containerPreviewWidth / 2)}px`;
          multiFrameWrapper.classList.add('mf-wrap');
          multiFrameWrapper.appendChild(canvas);

          containerEl.appendChild(multiFrameWrapper);
        } else {
          containerEl.appendChild(canvas);
        }
      }

      this.vuexSetPlayModelOptions({ width: this.containerAnimationWidth });

      this.$eventHub.$emit('drawPreviewFramesCompleted');
    },
    optionsChanged(value, postMessage = true) {
      if (!this.playModel) return;
      this.vuexSetOptions(value);

      if (typeof value.LabelsOverrideType !== 'undefined' || typeof value.LabelsOverrideMap !== 'undefined') {
        this.vuexSetPlayDataPlayerLabels(this.options.LabelsOverrideMap);
      }

      const playModelOptions = {};
      if (typeof value.CourtBackground !== 'undefined') {
        playModelOptions.background = value.CourtBackground === 'Hardwood' ? '' : '#FFF';
        playModelOptions.lineColor = value.CourtBackground === 'Hardwood' ? '#FFF' : '#000';
      }
      if (typeof value.MirrorMode !== 'undefined') {
        playModelOptions.mirror = value.MirrorMode;
      }
      if (typeof value.FlipPlayerLabels !== 'undefined') {
        playModelOptions.flipPlayerLabels = value.FlipPlayerLabels;
      }
      if (typeof value.PlaybackSpeed !== 'undefined') {
        playModelOptions.speed = value.PlaybackSpeed;
      }
      if (typeof value.Position !== 'undefined') {
        playModelOptions.position = value.Position;
      }
      if (typeof value.LinesDisplay !== 'undefined') {
        playModelOptions.linesDisplay = value.LinesDisplay;
      }
      if (typeof value.LinesDisplayOnMoveOnly !== 'undefined') {
        playModelOptions.linesDisplayOnMoveOnly = value.LinesDisplayOnMoveOnly;
      }
      if (typeof value.LinesHiddenIds !== 'undefined') {
        playModelOptions.linesHiddenIds = value.LinesHiddenIds;
      }
      if (typeof value.PlayersHiddenPositions !== 'undefined') {
        playModelOptions.playersHiddenPositions = value.PlayersHiddenPositions;
      }
      if (Object.entries(playModelOptions).length) {
        this.vuexSetPlayModelOptions(playModelOptions);
      }

      this.$eventHub.$emit('animationPause');
      this.drawPreviewFrames();
      this.$eventHub.$emit('animationRefreshFrame');

      if (postMessage) {
        this.postMessageToParentFrame({
          type: 'optionsChanged',
          payload: Object.fromEntries(Object.entries(value).filter(([key]) => key !== 'AnimationProgress')),
        });
      }

      this.$eventHub.$emit('optionsChanged');

      this.options.ShowFirstFrame ? this.vuexSetAnimationVisible() : this.vuexSetAnimationHidden();
    },
    postMessageToParentFrame(payload) {
      if (window.ReactNativeWebView) {
        return window.ReactNativeWebView.postMessage(JSON.stringify(payload));
      }
      window.parent.postMessage({ source: '@luceosports/animation-playback', ...payload }, '*');
    },
    async webViewRefreshFrames() {
      setTimeout(() => {
        this.windowRefreshSizeData();
        this.drawPreviewFrames();
        this.vuexSetPlayModelOptions({ width: this.containerAnimationWidth });
        this.$eventHub.$emit('animationRefreshFrame');
        this.$eventHub.$emit('preloaderHide');
        this.postMessageToParentFrame({
          type: 'webViewRefreshed',
        });
      }, 200);
    },

    webViewSetOptions(event) {
      this.optionsChanged(event.detail, false);
    },
    webViewSetTeamPlayers(event) {
      this.vuexSetTeamPlayers(event.detail, false);
    },
    windowRefreshSizeData() {
      this.window.height = window.innerHeight;
      this.window.width = window.innerWidth;
    },
    setPlayData({ detail: playData }) {
      this.vuexSetPlayData(playData);
      // this.windowRefreshSizeData();
      this.vuexSetPlayModel(this.containerWidth);
      this.vuexSetOptions({ AnimationProgress: 0 });
      this.optionsChanged(this.options, false);
    },
    animationCompleted() {
      this.postMessageToParentFrame({
        type: 'animationCompleted',
      });
    },
  },
};
</script>

<style>
.v-application.fullScreen .v-application--wrap {
  justify-content: center;
}
.v-application--wrap {
  align-items: center;
  justify-content: space-between;
}
.theme--dark.v-application {
  background: #303030 !important;
}
.play-canvas-container {
  display: flex;
  flex-wrap: wrap;
}
.mf-wrap:nth-child(odd) {
  padding-right: 2px;
}
.mf-wrap:nth-child(even) {
  padding-left: 2px;
}
.mf-wrap:nth-child(1) {
  padding-bottom: 2px;
}
.mf-wrap:nth-child(2) {
  padding-bottom: 2px;
}
.mf-wrap:nth-child(3) {
  padding-top: 2px;
}
.mf-wrap:nth-child(4) {
  padding-top: 2px;
}
.mf-wrap {
  display: inline-block;
  line-height: 10px;
}
.mf-wrap canvas {
  width: 100%;
}
</style>
